 @import url('https://fonts.googleapis.com/css2?family=Mulish:wght@300;400;500;600;700&display=swap');

 
 .wp-block-fbs-block-subscribe .subscribe {
     display: flex;
     flex-direction: column;
     align-items: center;
     justify-content: center;
     background: white;
     width: 100%;
     height: 474px;
     border-radius: 10px;
     box-shadow: 0 4px 20px rgba(61, 159, 255, 0.2);
 }
 
 
 .wp-block-fbs-block-subscribe .subscribe__title {
     font-weight: bold;
     font-size: 26px;
     margin-bottom: 18px;
 }
 
 .wp-block-fbs-block-subscribe .subscribe__copy {
     max-width: 450px;
     text-align: center;
     margin-bottom: 53px;
     line-height: 1.5;
 }
 
 .wp-block-fbs-block-subscribe .form {
     margin-bottom: 25px;
 }
 
 .wp-block-fbs-block-subscribe .form__email {
     padding: 13px 25px;
     border-radius: 5px;
      border: 1px solid #CAD3DB;
     width: 431px;
     font-size: 18px;
     color: #0F121F;
     margin-right: 10px;
 }
 
 .wp-block-fbs-block-subscribe .form__email:focus {
     outline: 1px solid #3D9FFF;
 }
 
 .wp-block-fbs-block-subscribe .form__button {
     background: #3D9FFF;
     padding: 10px;
     border: none;
     width: 244px;
     height: 65px;
     border-radius: 5px;
     font-size: 18px;
     color: white;
     box-shadow: 0 4px 20px rgba(61, 159, 255, 0.7);
 }
 
 .wp-block-fbs-block-subscribe .form__button:hover {
     box-shadow: 0 10px 20px rgba(61, 159, 255, 0.7);
 }
 
 .wp-block-fbs-block-subscribe .notice {
     width: 345px;
 }
 